import React, { useState, useEffect } from "react"
import axios from "axios"

import Layout from "../Template/Layout"
import Table from "../Template/Table"

const MachineDetails = ({ match }) => {
	const [machineDetails, setMachineDetails] = useState([])

	const {
		params: { name },
	} = match

	const machineName = name.replace(/\s+/g, "-").toLowerCase()

	const machineHeaders = [
		"ID",
		"Name",
		"Size",
		"Origin",
		"Override",
		"Class",
		"Machine Sites",
		"Weakness",
		"Strength",
		"Weak Points",
		"Explosive Components",
	]

	useEffect(() => {
		axios
			.get(`${process.env.REACT_APP_API_URL}/filter?name=${name}`)
			.then((response) => {
				console.log(response.data)
				setMachineDetails(response.data)
			})
	}, [])

	return (
		<Layout>
			<div className="machine-details-section">
				<h1 className="title mt-5 has-text-white">{name}</h1>
				<img
					src={require(`../../assets/machines/${machineName}.png`)}
					alt={`${name}`}
				/>
				<Table headers={machineHeaders} data={machineDetails} />
			</div>
		</Layout>
	)
}

export default MachineDetails
